@import '../../../scss/variables';
@import '../../../scss/mixins';
@media screen and (max-height: 700px) {
    .popPost-slider-wrap {
        margin-top: 5vh;
        .popPost-slider-wrap {
            font-size: 1.1rem;
        }
    }
    .popPost-card {
        width: 36vw;
        .popPost-title {
            font-size: 0.7em;
        }
        .popPost-club {
            font-size: 0.6em;
        }
    }
}

::-webkit-scrollbar {
    width: 0px;
}
.popPost-slider-wrap {
    position: relative;
    top: -100px;
}
.popPost-container {
    display: flex;
    overflow-x: scroll;
    // padding-left: 10px;
    width: 100%;
}
.popPost-sliderHeader {
    width: 100%;
    color: white;
    padding-left: 25px;
    padding-bottom: 10px;
    font-size: 1.3rem;
}

.popPost-card {
    flex-shrink: 0;
    // width: 150px;
    // height: 200px;
    width: 40vw;
    height: 25vh;
    border-radius: 10px;
    margin-left: 15px;
    margin-bottom: 10px;
    box-shadow: 3px 3px 3px 0px #0000005c;
    // background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .popPost-title {
        width: 100%;
        font-size: 1em;
        color: $colors-white;
        padding-left: 10px;
    }
    .popPost-club {
        width: 100%;
        color: rgba(246, 246, 246, 0.8);
        font-size: 0.8em;
        padding: 0px 0px 10px 10px;
    }
}
